import { Option } from './types';
import Checkbox from './Checkbox';
import { useCallback } from "react";

interface Props {
  options: Option[];
  selectedOptions: Option[];
  onChange: (selected: Option[]) => void;
  title: string;
  disabled?: boolean;
}

const CheckboxGroup = ({
  options,
  selectedOptions,
  onChange,
  title,
  disabled,
}: Props) => {
  const onOptionChange = useCallback(() => (value, checked) => {
    if (disabled) {
      return;
    }
    if (!checked) {
      return onChange(selectedOptions.filter(o => o.value !== value));
    }
    const checkedOption = options.find((o) => o.value === value);
    checkedOption && onChange([...selectedOptions, checkedOption]);
  }, [disabled, options, onChange]);
  return (
    <>
      {title && <h2>{title}</h2>}
      {options.map((o) => (
        <Checkbox
          key={o.value}
          checked={selectedOption && selectedOption.value === o.value}
          onChange={onOptionChange}
          value={o.value}
          label={o.label}
        />
      ))}
    </>
  );
};

export default CheckboxGroup;
